import { useRef, useState } from "react";
import { Box, Flex } from "@radix-ui/themes";

import Input from "../presentation/todoList/Input";
import Todos from "../presentation/todoList/Todos";
import Title from "../common/Title";



const TodoList = ({width}) => {
    const [todoList, setTodoList] = useState([]);

    const [input, setInput] = useState("");

    const id = useRef(0);
    
    const onChange = (id) => {
        setTodoList(todoList.map(todo => todo.id === id ? {...todo, isDone : !todo.isDone } : todo));
    };
    
    const onDelete = (id) => {
        setTodoList(todoList.filter(todo => todo.id !== id));
    };
    

    const onInput = (e) => {
        setInput(e.target.value);

    };

    const onAdd = () => {
        setTodoList([
            ...todoList, 
        
            { id : ++id.current, isDone : false, text : input }
        ]);

        setInput("");
    };


    return(
        <Box width={width} p={"3"} style={{boxShadow : "0px 2px 2px 2px white", borderRadius : "5%"}}>
            <Flex direction={"column"} gapY={"3"}>
                <Title size={"7"} text={"오늘의 일정"}/>
                <Input input={input} onAdd={onAdd} onInput={onInput}/>
                <Todos todoList={todoList} onChange={onChange} onDelete={onDelete}/>
            </Flex>
        </Box>
    );
};

export default TodoList;