import { Flex } from "@radix-ui/themes";
import { colors } from "../../styles/colors";

const Wiget = ({children}) => {
    return(
        <Flex direction={"column"} gapY={"2"} p={"3"} style={{boxShadow : "0px 2px 2px 2px " + colors.light_gray, borderRadius : "5%"}}>
            {children}
        </Flex>
    );
};

export default Wiget;