import { Box, Button } from "@radix-ui/themes";
import { colors } from "../../../styles/colors";

const TodoButton = ({id, isDone, onChange}) => {
    return(
        <Box>
            <Button style={{backgroundColor : colors.mint, color : colors.white}}
                onClick={() => onChange(id)}
            >
                {isDone ? "완료" : "미완료"}
            </Button>
        </Box>
    );
};

export default TodoButton;