import { Box, Text } from "@radix-ui/themes";
import { colors } from "../../../styles/colors";

const TodoText = ({text, isDone}) => {
    return(
        <Box>
            <Text style={{color : colors.white, textDecoration : isDone ? "line-through" : "none"}}>{text}</Text>
        </Box>
    );
};

export default TodoText;