import { Box } from "@radix-ui/themes";
import { colors } from "../../styles/colors";

const Screen = ({children}) => {
    return(
        <Box as={"div"} width={"100%"} height={"100vh"} p={"0"} m={"0"} style={{backgroundColor : colors.navy, backgroundImage : "url('background.jpg')", backgroundSize : "cover"}}>
            {children}
        </Box>
    );
};

export default Screen;