import { Box, Button, TextField } from "@radix-ui/themes";
import { colors } from "../../../styles/colors";

const Input = ({input, onInput, onAdd}) => {

    return(
        <Box>
            <TextField.Root placeholder="Etner the To DO" size={"3"} onChange={(e) => onInput(e)} value={input}>
                <TextField.Slot side={"right"}>
                    <Button style={{backgroundColor : colors.mint}}
                        onClick={onAdd}
                    >
                        Add
                    </Button>
                </TextField.Slot>
            </TextField.Root>
        </Box>
    );
};

export default Input;