import { Box, Flex } from "@radix-ui/themes";
import Todo from "./Todo";


const Todos = ({todoList, onChange, onDelete}) => {

    return(
        <Box>
            <Flex direction={"column"}>
                {todoList.map(todo => (
                    <Todo id={todo.id} todo={todo} onChange={onChange} onDelete={onDelete}/>
                ))}
            </Flex>
        </Box>
    );
};

export default Todos;