import { Box, Flex } from "@radix-ui/themes";
import Wiget from "./Wiget";
import Member from "../presentation/wiget/Member";
import DateTime from "../presentation/wiget/DateTime";
import Weather from "../presentation/wiget/Weather";
import Title from "../common/Title";

const WigetGroup = ({width}) => {
    return(
        <Box width={width} p={"3"}>
            <Flex direction={"column"} gapY={"3"}>
                <Wiget>
                    <Title size="5" text={"회원"}/>
                    <Member />
                </Wiget>
                
                <Wiget>
                    <Title size={"5"} text={"시간"}/>
                    <DateTime />
                </Wiget>
                
                <Wiget>
                    <Title size={"5"} text={"날씨"}/>
                    <Weather />
                </Wiget>
                
            </Flex>
        </Box>
    );
};

export default WigetGroup;